// Lookup service for unified dropdown/select operations
export const lookupService = {
  async getLookupItems({ type, category, search, page = 1, limit = 50, filters = {} }) {
    const params = new URLSearchParams({
      type,
      page: page.toString(),
      limit: limit.toString(),
    });

    if (search && search.length >= 2) {
      params.append("search", search);
    }

    if (category) {
      params.append("category", category);
    }

    // Add custom filters
    Object.entries(filters).forEach(([key, value]) => {
      if (value !== null && value !== undefined && value !== "") {
        params.append(`filters[${key}]`, value.toString());
      }
    });

    return http.instance.get(`/lookup?${params}`);
  },

  async getLookupItem(type, id) {
    return http.instance.get(`/lookup/${type}/${id}`);
  },

  async getSupportedTypes() {
    return http.instance.get("/lookup/types");
  },
};


